import { CART_ADD_ITEM,CART_REMOVE_ITEM,CART_SAVE_PAYMENT_METHOD,CART_SAVE_SHIPPING_ADDRESS,CART_CLEAR_ITEMS } from "../constants/cartConstants"; 

export const cartReducer = (state={cartItems:[],shippingAddress:{}},action) =>{
    switch(action.type){
        case CART_ADD_ITEM:
            const item = action.payload

            const existItem = state.cartItems.find((el) => el.product === item.product)

            if(existItem){
                return{
                    ...state,
                    cartItems: state.cartItems.map((el)=> el.product === existItem.product ? item : el)
                }
            }
            else{
                return{
                    ...state,
                    cartItems: [...state.cartItems,item]
                }
            }
        case CART_REMOVE_ITEM:
            return{
                ...state,
                //Removes all items where item.id == data.id
                cartItems: state.cartItems.filter((item) => item.product !== action.payload)
            }
        case CART_SAVE_SHIPPING_ADDRESS:
            return{
                ...state,
                shippingAddress: action.payload
            }
        case CART_SAVE_PAYMENT_METHOD:
            return{
                ...state,
                paymentMethod: action.payload
            }
        case CART_CLEAR_ITEMS:
                return {
                  ...state,
                  cartItems: [],
                }
        default:
            return state
    }

}